import * as THREE from "three";
import GLTFLoader from "three-gltf-loader";

const publiPath = process.env.PUBLIC_PATH;//vue.config.js中配置了该变量 webpack的功能
// console.log({publiPath})
var fromLL = function(lon, lat) {
    var extent = 20037508.34;

    var x = (lon * extent) / 180;
    var y =
      Math.log(Math.tan(((90 + lat) * Math.PI) / 360)) / (Math.PI / 180);
    y = (y * extent) / 180;

    return [(x + extent) / (2 * extent), 1 - (y + extent) / (2 * extent)];
  };
  var translate = fromLL(120.22450447, 30.23155861728); //萧山体育馆 白莲花

  var transform = {
    translateX: translate[0],
    translateY: translate[1],
    translateZ: 0,
    rotateX: Math.PI / 2,
    rotateY: 0,
    rotateZ: 0,
    scale: 50.41843220338983e-8 //设置模型大小
  };
class CustomLayer {
    constructor() {
      this.id = "custom_layer";
      this.type = "custom";

      this.camera = new THREE.Camera();
      this.scene = new THREE.Scene();

      const directionalLight = new THREE.DirectionalLight(0xffffff);
      directionalLight.position.set(0, -70, 100).normalize();
      this.scene.add(directionalLight);

      const directionalLight2 = new THREE.DirectionalLight(0xffffff);
      directionalLight2.position.set(0, 70, 100).normalize();
      this.scene.add(directionalLight2);

      var loader = new GLTFLoader();
      loader.load(
        // "http://localhost:8088/tile/buildingtower/34M_17.gltf",
        `${publiPath.length > 1 ? publiPath : ''}/model3d/buildingtower/34M_17.gltf`,
        function(gltf) {
          this.scene.add(gltf.scene);
        }.bind(this)
      );
    }

    onAdd(map, gl) {
      this.map = map;

      this.renderer = new THREE.WebGLRenderer({
        canvas: map.getCanvas(),
        context: gl
      });

      this.renderer.autoClear = false;
    }

    render(gl, matrix) {
      const rotationX = new THREE.Matrix4().makeRotationAxis(
        new THREE.Vector3(1, 0, 0),
        transform.rotateX
      );
      const rotationY = new THREE.Matrix4().makeRotationAxis(
        new THREE.Vector3(0, 1, 0),
        transform.rotateY
      );
      const rotationZ = new THREE.Matrix4().makeRotationAxis(
        new THREE.Vector3(0, 0, 1),
        transform.rotateZ
      );

      const m = new THREE.Matrix4().fromArray(matrix);
      const l = new THREE.Matrix4()
        .makeTranslation(
          transform.translateX,
          transform.translateY,
          transform.translateZ
        )
        .scale(
          new THREE.Vector3(
            transform.scale,
            -transform.scale,
            transform.scale
          )
        )
        .multiply(rotationX)
        .multiply(rotationY)
        .multiply(rotationZ);

      this.camera.projectionMatrix.elements = matrix;
      this.camera.projectionMatrix = m.multiply(l);
      this.renderer.state.reset();
      this.renderer.render(this.scene, this.camera);
      this.map.triggerRepaint();
    }
  }

  export default CustomLayer;