<template>
  <div style="height:100%;width:100%;text-align:left;">
    <div ref="mapboxContainer" :style="mapSize"></div>
  </div>
</template>
<script>
import "mapbox-gl/dist/mapbox-gl.css";
import mapboxgl from "mapbox-gl";
import CustomLayer from "@/components/mapbox/mapbox3dLayer/buildingLayer.js"
// import Threebox from "threebox";
// import turf from "@turf/turf";
// import gaode_makalong2 from "@/assets/mapboxStyle/gaode_makalong2.json";
// import dark-matter from "@/assets/mapboxStyle/dark-matter.json";
// import building from '@/assets/buildingtower/34M_17.gltf'
export default {
  props: {
    mapWidth: {
      type: String
    },
    mapHeight: {
      type: String
    }
  },
  data() {
    return {};
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      mapboxgl.accessToken =
        "pk.eyJ1IjoibHhxanNzIiwiYSI6ImNqY3NkanRjajB1MWwzM3MwcnA0dDJrYngifQ.ApTVfmm2zBM_kF22DvtowQ";
      const map = new mapboxgl.Map({
        container: this.$refs.mapboxContainer,
        // style: gaode_makalong2,
        style: "mapbox://styles/mapbox/dark-v9",
        center: [120.21093, 30.2263],
        zoom: 14.2,
        pitch: 60,
        heading: 41,
        hash: true
      });
      map.addControl(new mapboxgl.NavigationControl());
      map.on("load", function() {
        map.addLayer(//mapbox提供的建筑数据
          {
            id: "3d-buildings",
            source: "composite",
            "source-layer": "building",
            filter: ["==", "extrude", "true"],
            type: "fill-extrusion",
            minzoom: 11,
            paint: {
              "fill-extrusion-color": "#aaa",

              // use an 'interpolate' expression to add a smooth transition effect to the
              // buildings as the user zooms in
              "fill-extrusion-height": [
                "interpolate",
                ["linear"],
                ["zoom"],
                12,
                0,
                12.05,
                ["get", "height"]
              ],
              "fill-extrusion-base": [
                "interpolate",
                ["linear"],
                ["zoom"],
                12,
                0,
                12.05,
                ["get", "min_height"]
              ],
              "fill-extrusion-opacity": 0.6
            }
          }
        );
        map.addLayer(new CustomLayer());
      });
    }
  },
  computed: {
    mapSize() {
      let styleObj = {
        width: this.mapWidth,
        height: this.mapHeight
      };
      return styleObj;
    }
  }
};
</script>
